body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  
  flex-direction: column;
  text-align: center;
  align-items: stretch;
  position: relative;
  min-height: 100vh;
  width:100%
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.viewContent {
  padding-bottom: 12.5rem;
  width: 100%;
  justify-content: center;
}

/* CORRELLATION LIST STYLING */

.CorrellationList {
  border: 3px solid blue;
  display: flex;
  flex-direction: column;
  width: 1;
  margin: auto;
  margin-bottom: 10px;
  align-items: center;
  justify-content: center;
  
}

.CorrellationContainer {
  
  width: 100%;
  
}

.CorrellationItem {
  width: 95%;
  margin: 5px;
  min-width: 280px;
}

.percentContainer {
  border: 1px solid black;
  margin-bottom: 25px;
}

.ItemPercent {
  background-color: blue;
}

/* MODAL STYLING */

.modal {
  
  position: fixed;
  top: 0;
  left: 0;
  width:5%;
  height: 100%;
  background: rgba(248, 246, 246, 0.8);
  
}

.modal-main > button {
  align-self: flex-end;
}

.modal-main > p {
  align-self: left;
  width: 5;
  white-space: pre-line;
  display: inline-block;
word-break: break-word;
    
  font-size: 80%;
  height: 60%
}

.modal-main {
  
  display: flex;
  flex-direction: column;
  position:fixed;
  background: white;
  width: 80%;
  height: 60%;
  
  
  top:50%;
  left:50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
}

.display-block {
  width:100%;
  display: block;
}

.description{
  font-family:Courier; 
  color:Blue; 
  font-size: 5px;
  height:50px;
  word-break: break-all;
}

.display-none {
 
  width:100px;
  display: none;
}

.buttonSpace {
  justify-content: center;
}

/* HEADER STYLING */

header {
  display: flex;
  border: 3px solid black;
  justify-content: space-between;
}

header > img {
  height: 100px;
}

.headerLink {
  text-decoration: none;
  color: black;
}

.headerText {
  align-self: center;
}

/* FOOTER STYLING */

footer {
  border-top: 3px solid black;
  width: 100%;
  position: absolute;
  bottom: 0;
  height: 9.5rem;
  margin: 0
}

.Results{
  
  padding-bottom: 3%;
  padding-top: 2%
}

.Search-Main{
  display: block;
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.Search-2{
  width: 100%
}

.Search-3{
  padding-bottom: 4%
}

.Landing-Text{
  font-size: 80%;
  padding-top: 2%;
  padding-bottom: 3%
}

.popup-main{
  width: 50%;
  border: 3px solid black;
  height: 20%;
  white-space: pre-line;
  overflow-wrap: normal;
}

.popup {
  
  height: 50%
  
}
